import Component from "./Component";
export default class Camera extends Component {
    constructor(params) {
        super('camera', "camera", params);
        this.zoom = 1;
        this.index = -1;
    }
    onAdd(game, gameObject) {
        this.index = game.addCamera(this.gameObject);
    }
    predraw(game, ctx) { }
    draw(game, ctx) { }
    postdraw(game, ctx) { }
    onPredraw(game, ctx) {
        game.cameraRenderIndex = this.index;
        game.predraw(game, ctx);
    }
    onDraw(game, ctx) {
        game.draw(game, ctx);
    }
    onPostdraw(game, ctx) {
        game.postdraw(game, ctx);
    }
}
Component.registerComponent('camera', Camera);
